

import App from './App.vue';
const Digivigil = defineAsyncComponent(() => import('./components/digivigil/Main.vue'))
const Balek = defineAsyncComponent(() => import('./components/balek/Main.vue'))
const Diaplode = defineAsyncComponent(() => import('./components/diaplode/Main.vue'))

const About = defineAsyncComponent(() => import('./components/about/Main.vue'))
const AboutProgression = defineAsyncComponent(() => import('./components/about/progression/Main.vue'))
const AboutVideos = defineAsyncComponent(() => import('./components/about/videos/Main.vue'))
const AboutFocus = defineAsyncComponent(() => import('./components/about/focus/Main.vue'))


import {createApp, defineAsyncComponent} from 'vue';
import { RouteRecordRaw, createRouter, createWebHashHistory } from 'vue-router'




// 2. Define some routes
// Each route should map to a component.
// We'll talk about nested routes later.
const routes = [
    { path: '/', component: Digivigil },
    { path: '/balek', component: Balek,
    meta: { transition: 'fade'}},
    { path: '/diaplode', component: Diaplode },
    { path: '/about', component: About,
        children: [
            {
                path: 'focus',
                component: AboutFocus,
            },
            {
                path: 'progression',
                component: AboutProgression,
            },
            {
                path: 'videos',
                component: AboutVideos,
            }
        ],},
]

// 3. Create the router instance and pass the `routes` option
// You can pass in additional options here, but let's
// keep it simple for now.
const router = createRouter({
    // 4. Provide the history implementation to use. We are using the hash history for simplicity here.
    history: createWebHashHistory(),
    routes, // short for `routes: routes`
})



createApp(App).use(router).mount('#main')
